






























































































































































@import "@/assets/scss/_variable.scss";

.h1 {
  font-size: 32px;
  margin-bottom: 20px;
  font-weight: bold;
}
.progress {
  height: 30px;
  width: 100%;
  &-report {
    line-height: 1.5;
    text-align: end;
  }
}

.divider {
  padding-top: 30px;
  padding: 50px 20px 20px 20px;
  margin: auto;
  background-color: $sub;
  position: relative;
}

.fixBox {
  position: absolute;
  z-index: 2;
  top: 20px;
  right: 20px;
  display: flex;
  // transform: translateX(100%);

  .download-json {
    margin-right: 10px;
  }
}
.file-loader {
  margin-bottom: 20px;
}
.filters {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0;
  &__list {
    padding: 0;
    margin: 0;
  }
}
.index-filterBoard {
  min-width: 750px;
  width: 60%;
  height: 80%;
}

.d-flex {
  display: flex;
}
