









































































.fileLoader {
  &__upload {
    cursor: pointer;
    margin-bottom: 10px;
  }
  &__check {
    cursor: pointer;
    &:hover {
      color: #0075ff;
    }
  }
}
.upload {
  display: block;
  font-size: 28px;
  max-width: 500px;
  min-height: 200px;

  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  &:hover {
    color: orange;
  }
}
