











































































































































.outputs {
  &__line {
    border-radius: 10px 10px 0 0;
    transition: box-shadow 0.2s, transform 0.2s;

    border-bottom: 1px solid #000;
    margin-bottom: 10px;
    &:hover {
      transform: translate3d(0, -5%, 0);
      box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.2);
      .outputs__line__name {
        text-decoration: underline;
        color: #0075ff;
      }
    }
    &__name {
      user-select: none;
      align-self: stretch;
      display: flex;
      align-items: center;
    }
  }
}
