// Color ----

$gray-200: #f2f2f2;
$gray-300: lightgray;
$gray-400: #b2b2b2;
$gray-500: #adb5bd;
$gray-800: gray;
$gray-900: #2f2f2f;
$black: #000;

$red-800: #ad0a1a;
$red-700: #ff0019;
$red-500: #fa5869;
$red-300: #fe586a;
$red-200: #eb9da5;

$orange: #ff5722;
$orange-500: orange;

$blue-200: #92beff;
$blue-700: #3989ff;
$blue-800: #0d6efd;
// $green      : green;
$green-400: #07aa07;
$green-200: #6ed786;

$deep: #000000;
$main: #FCA311;
$contrast: #14213D;
$sub: #E5E5E5;
$light: #FFFFFF;

$lightSub: $gray-400;
$darkSub: $gray-900;
$green: #00988e;
$yellow: #fcb643;

// Body  ----
$main-bg: $sub;
$body-bg: $contrast;
$body-color: $black;
$footer-bg: $gray-400;
$footer-color: $gray-900;


// 000000
// 14213D
// FCA311
// E5E5E5
// FFFFFF

$lightup: $orange;
$alert: $red-800;
$alert-sub: $red-200;
$success: $green;
$info: $blue-800;
$darken: $gray-400;


// Links ----
$link-color: $info;
$link-decoration: none;
$link-hover-color: $lightup;
$link-hover-decoration: underline;
$link-visited-color: $body-color;
$link-pseudo-content: ">>";

// alert
$alert-bg-color: $alert-sub;
$alert-font-color: $alert;
$alert-border-color: $red-300;

// success
$success-bg-color: $green-200;
$success-font-color: $green;
$success-border-color: $green-400;

// info
$info-bg-color: $blue-200;
$info-font-color: $blue-800;
$info-border-color: $blue-700;

// sub
$sub-bg-color: $gray-200;
$sub-font-color: $sub;
$sub-border-color: $gray-500;

// Typography ----
//
// Font, line-height, and color for body text, headings, and more.
$font-family-base: "Noto Sans TC", sans-serif;

$font-size-base: 1em;
$font-size-lg: $font-size-base * 1.25;
$font-size-sm: $font-size-base * 0.875;
$icon-size-base: 2rem;

$line-height-base: 1.5;
$line-height-lg: 2;
$line-height-sm: 1.25;

$font-weight-base: 400;
$font-weight-bold: 700;
$font-weight-bolder: bolder;

$h1-font-size: $font-size-base * 2.5;
$h2-font-size: $font-size-base * 2;
$h3-font-size: $font-size-base * 1.75;
$h4-font-size: $font-size-base * 1.5;
$h5-font-size: $font-size-base * 1.25;
$h6-font-size: $font-size-base;

$footer-font-size: $font-size-sm;

// transition
$transition-base: all 0.2s;

// HTML box default ----
//
// Define common padding and border radius sizes and more.
$border-width: 1px;
$border-color: $sub;
$border-radius: 5px;

$box-shadow: 5px 5px 10px 0 $sub;

$padding-horizontal: 20px;
$padding-horizontal-sm: 10px;
$padding-vertical: 10px;

$space-horizontal: 20px;
$space-vertical: 10px;

$block-list-padding: $padding-vertical $padding-horizontal;

$container-width-full: 1920px; // ($col-width + $gutter*2)*$cols;
$container-width-lg: 1080px; // ($col-width + $gutter*2)*$cols;
$container-width-md: 768px; // ($col-width + $gutter*2)*$cols;
// $container-width-md :  960px;
// $container-width-sm :  540px;

// $nav-line-height: 50px;

// $nav-bg-hover-color : $sub;

