// Components each setting

// Component : block

$block-padding-horizontal: 20px;
$block-padding-vertical: 10px;
//$block-title-size         : $h4-font-size;
$block-title-sub-size: $font-size-sm;
$block-title-sub-color: $sub;
$block-title-icon-size: $icon-size-base;

$block-body-font-size: $font-size-base;

$block-foot-font-size: $font-size-sm;
$block-foot-font-color: $sub;

// Component : menu

$menu-side-width: 100px;

// Component : bs4 - Cards

$card-spacer-y: 0.75rem !default;
$card-spacer-x: 1.25rem !default;
$card-border-width: 1px; //$border-width !default;
$card-border-radius: 5px; //$border-radius !default;
$card-border-color: $sub; //rgba($black, .125) !default;
$card-inner-border-radius: 5px; //subtract($card-border-radius, $card-border-width) !default;
$card-cap-bg: $black; //rgba($black, .03) !default;
$card-cap-color: null !default;
$card-height: null !default;
$card-color: null !default;
$card-bg: $contrast; //$white !default;

$card-img-overlay-padding: 1.25rem !default;

// $card-group-margin:                 $gutter;  //$grid-gutter-width / 2 !default;
$card-deck-margin: 10px; //$card-group-margin !default;

$card-columns-count: 3 !default;
$card-columns-gap: 1.25rem !default;
$card-columns-margin: 10px; //$card-spacer-y !default;

$enable-rounded: true;

// breakpoint
//$mobile-first : false;      // min-width : max-width
$breakpoint-mobile: 35.5em;
$breakpoint-pad: 48em;
$breakpoint-screen: 64em;
$breakpoint-screen-wide: 80em;

$form-input-focus-outline: 2px solid $blue-200;
