


















































































































































































.group {
  margin-bottom: 20px;

  border-bottom: 1px solid #000;
  padding: 10px;
}
.filter {
  display: flex;
  margin-bottom: 10px;
}
.board {
  background: #d7d7d7;
  padding: 20px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  &__header {
    width: 100%;
    // background-color: #14213d;
    border-radius: 20px 20px 0 0;
    padding: 20px;
    // margin-bottom: 20px;
    display: flex;
    flex-wrap: wrap;

    &__filename {
      flex: 1;
      margin-right: 10px;
      font-size: 20px;
    }
    &__btns {
    }
    &__insert-group {
      width: 100%;
      margin-top: 10px;
      .btn--main {
        margin-right: 10px;
      }
    }
  }
  &__body {
    width: 100%;

    border-radius: 20px;
    padding: 20px;
    flex: 1;
    overflow-y: auto;
    margin-bottom: 10px;
  }
  &__footer {
    display: flex;
    justify-content: flex-end;
  }
}
