




















































@import "@/assets/scss/_variable.scss";
.confirm {
  background-color: $sub;
  max-width: 500px;
  padding: 20px;
  font-size: 24px;
  &__title {
    font-weight: bold;
    margin-bottom: 15px;
  }
  &__msg {
    font-size: 0.75em;
    margin-bottom: 25px;
  }
  &__btns {
    font-size: 0.75em;
    display: flex;
    justify-content: flex-end;
    &__main {
      margin-left: 10px;
    }
  }
}
